<template>
  <div class="HotSong">

    <!-- 热歌榜 -->

    <MusicHitCharts></MusicHitCharts>

    <div class="min" >
      <PlayListBottom
        v-for="(item, index) in hotDataItems"
        :key="index"
        :item="item"
        :index="index"
        :currentMusic="$attrs.currentMusic"
        :Paused="$attrs.Paused"
        @update-music="update"
      ></PlayListBottom>
    </div>

    <MusicLogo>网易云</MusicLogo>

  </div>
</template>

<script>
import MusicHitCharts from '@/components/MusicHitCharts.vue';
import PlayListBottom from '@/components/PlayListBottom.vue';
import MusicLogo from '@/components/MusicLogo.vue'

export default {
  components:{
    MusicHitCharts,
    PlayListBottom,
    MusicLogo
  },
  data: function(){
    return {
      hotDataId:[],
      hotDataItems:[],
    }
  },
  created() {
    this.$emit('music:condition',true)
    this.axios.get("/top/list?idx=1").then((respaonse)=>{
      for(let i=0; i<20; i++){
        this.hotDataId.push(respaonse.data.playlist.trackIds[i].id)
      }
      this.axios.get(`/song/detail?ids=${this.hotDataId.join(",")}`).then((respaonse)=>{
          let result = respaonse.data.songs
          for(let i=0;i<result.length;i++){
            let obj = {
              id:result[i].id,
              name:result[i].name,
              picUrl:result[i].al.picUrl,
              artists:result[i].ar
            };
            this.hotDataItems.push(obj);
            this.$emit('music:condition',false)
          }
      })
    });
  },
  methods: {
    update(n) {
        this.$emit('update-music',{
            item:n.item,
            index:n.index,
            newSongs:this.hotDataItems,
        })
    }
  },
}
</script>

<style scoped lang="less">
    .HotSong {
        background-color: #fcfcfd;
        .min {
          min-height: 960px;
        }
    }
</style>

